export default {
	data() {
		return {
			userPhone: '',
			inviteCode: '',
			inviteLevel: {},
			msg: '',
			shareLevel: []
		};
	},
	computed: {
		phoneSafe() {
			return this.userPhone.substr(0, 3) + "****" + this.userPhone.substr(7);
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') { // 来自页面内分享按钮
			console.log(res.target)
		}
		return {
			title: '视频一键去水印，支持抖音、火山、快手、头条、西瓜、微博、美拍、映客、陌陌、微视等，方便又快捷！',
			path: '/pages/index/index',
			imageUrl: '../../static/images/banner1.png'
		}
	},
	onLoad(e) {
		this.getTask();
		this.userPhone = e.num;
		this.getUserInfo();
	},
	methods: {
		getUserInfo() {
			this.$uniRequest(
				this.$baseUrl + '/v1/member/info',
				0,
				(res) => {
					this.userInfo = res.data.data;
				},
				false
			)
		},
		getTask() {
			this.$uniRequest(
				this.$baseUrl + '/v1/member/invite',
				0,
				(res) => {
					if (res.data.code == 200) {
						var data = res.data.data;
						this.inviteCode = data.invite_code;
						this.inviteLevel = data.invite_level;
						this.msg = data.msg;
						this.shareLevel = data.share_level;
					}
				},
				false
			)
		}
	}
}
